<template>
  <!-- Sign Up component displayed in Sign Up View -->
  <div id="signUp">
    <b-container fluid>
      <b-card no-body class="w-100">
        <b-row no-gutters>
          <!-- Sign Image -->
          <b-col lg="6">
            <div class="signImg" :style="imgSrc">
              <span></span>
            </div>
          </b-col>
          <!-- SignUp Form -->
          <b-col lg="6" class="py-5">
            <b-card-body>
              <b-form @submit="submit">
                <div>
                  <!-- Intro -->
                  <h1>Create New Account</h1>
                  <p
                    class="text-secondary"
                  >Lorem ipsum dolor sit amet consectetur adipisicing elit. Laudantium molestiae</p>
                </div>
                <!-- User Email -->
                <b-form-group label="Email address:" label-for="userEmail">
                  <b-form-input
                    label="Email Address"
                    id="userEmail"
                    v-model="userEmail"
                    type="email"
                    required
                    placeholder="company@company.com"
                  ></b-form-input>
                </b-form-group>
                <!-- User Password -->
                <b-form-group>
                  <label for="password">Password</label>
                  <b-input
                    type="password"
                    id="password"
                    minlength="8"
                    maxlength="20"
                    v-model="userPass"
                    placeholder="8-20 Characters"
                  ></b-input>
                  <button class="showPass">
                    <i class="fa fa-eye"></i>
                  </button>
                </b-form-group>
                <b-form-group>
                  <label for="confirmPAss">Confirm Password</label>
                  <b-input
                    type="password"
                    id="confirmPass"
                    minlength="8"
                    maxlength="20"
                    v-model="userConfirmPass"
                    placeholder="Confirm your password"
                  ></b-input>
                  <button class="showPass">
                    <i class="fa fa-eye"></i>
                  </button>
                </b-form-group>
                <b-row>
                  <!-- Submit Button -->
                  <b-col lg="4">
                    <b-button type="Submit" class="px-4 py-2">Sign Up</b-button>
                  </b-col>
                  <!-- Sign In Button -->
                  <b-col lg="8">
                    <p>
                      Already have an account?
                      <b-nav-item href="#">Signin</b-nav-item>
                    </p>
                  </b-col>
                </b-row>
              </b-form>
            </b-card-body>
          </b-col>
        </b-row>
      </b-card>
    </b-container>
  </div>
</template>

<script>
import { mapActions } from "vuex";
export default {
  name: "SignComp",
  data() {
    return {
      userEmail: "",
      userPass: "",
      userConfirmPass: "",
      imgSrc: {
        backgroundImage: "url(" + require("../assets/sign.jpg") + ")"
      }
    };
  },
  methods: {
    ...mapActions(["addNewUser"]),
    submit(e) {
      e.preventDefault();
      // if user Password and the confirmation doesn't matched. show alert
      // Validation of email input type, password input type, confrim password input type and fulfiling all inputs is performed by default in the browser
      if (this.userPass !== this.userConfirmPass) {
        alert("Passwords don't match, please re-enter your password");
      } else {
        // If all set, add new user to the state
        this.addNewUser({
          mail: this.userEmail,
          password: this.userPass,
          signedIn: false
        });
      }
      // Empty fields after submitting
      this.userEmail = "";
      this.userPass = "";
      this.userConfirmPass = "";
    }
  }
};
</script>

<style scoped>
#signUp {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  min-height: 100vh;
  padding-top: 10vh;
  background-color: var(--var-gray);
}

.card {
  border: none;
}

.row.no-gutters {
  min-height: 80vh;
}

.signImg {
  min-height: 80vh;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}

.card-body {
  height: 100%;
  padding-right: 20%;
}

form {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.form-group {
  position: relative;
}

.showPass {
  position: absolute;
  top: 39px;
  right: 13px;
  background: transparent;
  border: none;
}

button[type="submit"] {
  background-color: var(--var-gray);
  color: var(--var-gold);
  border: none;
}

button[type="submit"]:active,
button[type="submit"]:hover,
button[type="submit"]:focus {
  color: var(--var-gray) !important;
  background-color: var(--var-gold) !important;
}

.nav-item {
  display: inline;
}

a.nav-link {
  color: var(--var-gold);
  font-weight: bold;
  display: inline;
  padding: 0;
}

@media (min-width: 768px) {
  form {
    font-size: 150%;
  }

  form p {
    font-size: 80%;
  }

  button[type="submit"] {
    font-size: 1.1rem;
  }
}
</style>